* {
    margin: 0;
    padding: 0;
}
body {
    background-color: #181818;
    color: white;
}
p {
    color: #d2d2d2;
}
a {
    color: inherit;
    text-decoration: none;
}
.main {
    max-width: 1000px;
    width: 1000px;
    margin: 0 auto;
    display: flex;
}
.sidebar {
    background-color: #202020;
    max-width: 300px;
    box-shadow: 5px 0px 15px rgba(5, 5, 5, 0.1);
    flex: 1;
    display: flex;
    flex-direction: column;
    &__inner {
        padding: 0 15px;
        display: flex;
        align-items: center;
        flex-direction: column;
    }
    &__top {
        position: relative;
    }
    &__foto-wrapper {
        position: absolute;
        margin-top: -62px;
        left: calc(50% - 70px);
    }
    &__title {
        margin-top: 68px;
        font-family: "Roboto";
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 21px;
        text-align: center;
    }
    &__subtitle {
        margin-top: 3px;
        color: #dedede;
        font-family: "Roboto";
        font-style: normal;
        font-weight: 300;
        font-size: 14px;
        line-height: 16px;
        text-align: center;
    }
    &__social {
        position: relative;
        display: flex;
        width: 100%;
        justify-content: center;
        margin-top: 18px;
        padding-bottom: 30px;
        &-inst {
            margin-right: 15px;
            display: block;
            width: 25px;
            height: 25px;
            background-image: url(../images/inst.svg);
        }
        &-vk {
            display: block;
            width: 25px;
            height: 25px;
            background-image: url(../images/vk.svg);
        }
        &-pinterest {
            margin-left: 15px;
            display: block;
            width: 25px;
            height: 25px;
            background-image: url(../images/pint.svg);
        }
    }
    &__social::after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 1px;
        background-color: #1c1c1c;
    }
    &__description {
        position: relative;
        font-family: "Roboto";
        font-style: normal;
        font-weight: 400;
        font-size: 13px;
        line-height: 150%;
        text-align: center;
        color: #ffffff;
        margin-top: 30px;
        padding-bottom: 30px;
    }
    &__description::after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 1px;
        background-color: #1c1c1c;
    }
    &__buttons {
        margin-top: 30px;
    }
    &__btn {
        padding: 13px 25px;
        border: none;
        background-color: #3137c9;
        color: white;
        border-radius: 25px;
        font-family: "Roboto";
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 14px;
        text-align: center;
        box-shadow: 0px 7px 17px -10px rgba(242, 63, 63, 0.2);
        cursor: pointer;
    }
    .clr-red {
        margin-right: 20px;
        background-color: #ed3024;
    }
}
.rightContentWrap {
    display: flex;
    flex-direction: column;
}
.navigation {
    &__wrap {
        background-color: #0d0d0d;
        box-shadow: 0px 5px 22px rgba(0, 0, 0, 0.25);
        width: 100%;
        display: flex;
        justify-content: space-between;
        position: relative;
        height: 40px;
        align-items: center;
        margin-top: 2px;
    }
    &__menu {
        z-index: 2;
        display: flex;
        list-style-type: none;
    }
    &__item {
        position: relative;
        font-family: "Roboto";
        font-style: normal;
        font-weight: 400;
        font-size: 11px;
        line-height: 13px;
        text-transform: uppercase;
        padding: 13px;
    }
    &__item-profil {
        display: flex;
        align-items: center;
        padding-right: 0;
    }
    &__item-rect {
        padding-right: 29px;
    }
    &__item-rect:hover .navigation__menu-article {
        display: block;
    }
    &__item-rect:hover {
        background-color: #3137c9;
    }
    &__item-rect::after {
        content: "";
        margin-left: 9px;

        width: 10px;
        height: 5px;
        position: absolute;
        top: calc(50% - 3px);
        background-image: url(../images/rect.svg);
    }
    &__menu-article {
        display: none;
        position: absolute;
        flex-direction: column;
        list-style-type: none;
        padding: 12px;
        background-color: #3137c9;
        box-shadow: 0px 6px 8px rgba(0, 0, 0, 0.2);
        border-radius: 0px 0px 5px 5px;
        max-width: 165px;
        margin-left: -13px;
        margin-top: 13px;
        min-width: 165px;
        .navigation__item {
            margin: 10px 0;
            padding: 0;
            text-transform: none;
            font-size: 12px;
            line-height: 14px;
            white-space: nowrap;
        }
        .navigation__item:first-child {
            margin-top: 0;
        }
        .navigation__item:last-child {
            margin-bottom: 0;
        }
    }
    &__search {
        background-color: #202020;
        height: 40px;
        font-family: "Roboto";
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 14px;
        padding-left: 20px;
        min-width: 212px;
        color: rgba(255, 255, 255, 0.4);
        margin-left: 30px;
        outline: none;
        border: 1px solid #202020;
    }
    &__search[type="text"]:focus {
        color: #fff;
        border: 1px solid rgb(45, 45, 45);
    }
}

.main-content {
    width: 600px;
    margin: 0 auto;
}
.picture {
    &__story {
        margin-top: 60px;
    }
    &__list {
        list-style-type: none;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__item {
        position: relative;
        cursor: pointer;
    }
    &__title {
        position: absolute;
        font-family: "Lato";
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 14px;
        color: #ffffff;
        margin-left: 14px;
        margin-top: 14px;
        z-index: 1;
    }
    &__storyImg {
        width: 140px;
        height: 195px;
        border-radius: 8px;
        filter: drop-shadow(0px 5px 15px rgba(0, 0, 0, 0.2));
    }
    &__storyDate {
        position: absolute;
        font-family: "Lato";
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 14px;
        text-align: right;
        text-transform: uppercase;
        color: #ffffff;
        right: 0px;
        margin-right: 12px;
        bottom: 16px;
    }
}
.addMessage {
    margin-top: 30px;
    background-color: #121212;
    box-shadow: 0px 15px 25px rgba(0, 0, 0, 0.45);
    border-radius: 5px;
    display: flex;
    justify-content: space-between;
    &__input {
        background-color: #121212;
        padding: 28px;
        font-family: "Roboto";
        font-style: normal;
        font-weight: 400;
        font-size: 15px;
        line-height: 18px;
        padding-left: 20px;
        flex: 1;
        margin-right: 20px;
        color: #d2d2d2;
        outline: none;
        border: 1px solid #121212;
        max-width: 447px;
        flex-wrap: wrap;
        resize: none;
    }
    &__input[type="text"]:focus {
        color: #fff;
        border: 1px solid rgb(45, 45, 45);
    }
    &__buttons {
        display: flex;
        align-items: center;
    }
    &__img {
        background-color: #ebebeb;
        width: 44px;
        height: 44px;
        border-radius: 50%;
        text-align: center;
        border: none;
        cursor: pointer;
    }
    &__send {
        background-color: #3137c9;
        width: 44px;
        height: 44px;
        border-radius: 50%;
        text-align: center;
        border: none;
        margin-right: 20px;
        margin-left: 15px;
        cursor: pointer;
    }
    &__send-img {
        margin-top: 2px;
        margin-left: 2px;
    }
}
.articles {
    &__list {
        list-style-type: none;
    }
    &__item {
        margin-top: 30px;
        background-color: #202020;
        box-shadow: 0px 15px 25px rgba(0, 0, 0, 0.35);
        border-radius: 5px;
    }
    &__picture {
        width: 100%;
    }
    &__video {
        cursor: pointer;
        position: relative;
        display: block;
    }
    &__video::before {
        content: "";
        width: 68px;
        height: 75px;
        left: 50%;
        margin-left: -34px;
        top: 50%;
        margin-top: -37.5px;
        display: block;
        position: absolute;
        z-index: 2;
        background-image: url(../images/videoPlay.svg);
    }
    &__item-inner {
        padding: 25px;
    }
    &__title {
        font-family: "Roboto";
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 21px;
    }
    &__description {
        margin-top: 15px;
        font-family: "Roboto";
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 21px;
        color: #d2d2d2;
    }
    &__footer {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 21px;
    }
    &__footer-Wrapp {
        font-family: "Lato";
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 14px;
        color: #828282;
    }
    &__date {
    }
    &__categories {
        margin-left: 35px;
        font-size: 15px;
        position: relative;
    }
    &__categories::before {
        content: "";
        position: relative;
        left: -10px;
        top: 0;
        height: 6px;
        width: 6px;
        background-color: #c4c4c4;
        display: inline-block;
        border-radius: 50%;
    }
    &__link {
        font-family: "Roboto";
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 16px;
        text-align: right;
        color: #107eff;
        text-decoration: none;
    }
}
.NavBtn {
    margin: 75px 0;
    text-align: center;
    &__btn:hover {
        box-shadow: 0px 0px 10px rgba(58, 108, 234, 0.15);
    }
    &__btn {
        margin: 5px;
        width: 40px;
        height: 40px;
        background-color: #0d0d0d;
        box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.15);
        border-radius: 3px;
        font-family: "Roboto";
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 16px;
        text-align: center;
        color: white;
        border: none;
        cursor: pointer;
    }
    &__btn:nth-child(3) {
        background-color: #104be3;
        box-shadow: 0px 0px 10px rgba(58, 108, 234, 0.15);
    }
    &__btn:nth-child(1) {
        background-color: #1a1a1a;
        color: #373737;
    }
}
